<template>
  <BaseDialog v-if="inputInvalid" title="Invalid Input" @close="closeDialog">
    <template #default>
      <p>Unfortunately, at least one input value is invalid!</p>
      <p>
        Please check all inputs and make sure you enter at least a few
        characters into each input field.
      </p>
    </template>
    <template #actions>
      <BaseButton @click="closeDialog">Okay</BaseButton>
    </template>
  </BaseDialog>
  <BaseCard>
    <form @submit.prevent="submitData">
      <div class="form-control">
        <label for="title">Title:</label>
        <input type="text" id="title" name="title" ref="titleInput" />
      </div>
      <div class="form-control">
        <label for="description">Description:</label>
        <textarea
          name="description"
          id="description"
          rows="3"
          ref="descInput"
        ></textarea>
      </div>
      <div class="form-control">
        <label for="link">Link:</label>
        <input type="url" id="link" name="link" ref="linkInput" />
      </div>
      <div>
        <BaseButton type="submit">Add</BaseButton>
      </div>
    </form>
  </BaseCard>
</template>

<script>
import BaseCard from "../UI/BaseCard";
import BaseButton from "../UI/BaseButton";
import BaseDialog from "../UI/BaseDialog";
import axios from 'axios';

export default {
  inject: ["addResource"],
  data() {
    return {
      inputInvalid: false,
    };
  },
  components: {
    BaseCard,
    BaseButton,
    BaseDialog,
  },
  methods: {
    submitData() {
      const enteredTitle = this.$refs.titleInput.value;
      const enteredDescription = this.$refs.descInput.value;
      const enteredLink = this.$refs.linkInput.value;
      if (
        enteredTitle.trim() === "" ||
        enteredDescription.trim() === "" ||
        enteredLink.trim() === ""
      ) {
        this.inputInvalid = true;
        return;
      } else {
          axios.post('https://vue-learning-cd69c-default-rtdb.europe-west1.firebasedatabase.app/', {
            title: this.enteredTitle,
            description: this.enteredDescription,
            link: this.enteredLink
          }).then((response)=>{
            console.log(response);
          }).catch((error)=>{
            console.log(error);
          })
      }
    },
    closeDialog() {
      this.inputInvalid = false;
    },
  },
};
</script>

<style scoped>
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
  text-align: left;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.15rem;
  border: 1px solid #ccc;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a0061;
  background-color: #f7ebff;
}

.form-control {
  margin: 1rem 0;
}
</style>
